import React, { PureComponent } from "react";
import SocialActions from "../../Elements/SocialActionHover";
import CtaButton from "../../Elements/CtaButton";
import { RATINGS_PATH } from '../../../../constants'
export default class CardHover extends PureComponent {
  render() { 
    const {item, wall, network} = this.props;
    const isShopIcon = (item.ugc_products && item.ugc_products.length > 0 && item.hotspot === 0) ? true : false;
    const nerworkColor = wall.ThemeRule.iconType == 505 ? network.color : wall.ThemeRule.iconColor;
    const hoverColor = { backgroundColor:wall.ThemeRule.postHover == "" || wall.ThemeRule.postHover != 1 ? "rgb(255, 255, 255, 0.8)" : "rgba(0, 0, 0, 0.8)"};
    const styleCta ={fontFamily:wall.ThemeRule.css_font}; const networkIcon = network.icon.replace('fa-', '');
    return (
        <div className="ts_gs_social_warpper">
          <div className="ts_gs_social_wrappeer_in">
            <div className="ts_gs_social_" style={{ color: 'rgb(255,255,255)'}}>
            {(item.rating != null && item.rating != 0) ? <div className="ts_gs_social_rating" style={{backgroundImage: `url(${RATINGS_PATH}${item.networkId}/${item.rating}.png)`}} ><div></div></div>
             : <div className={`tb__icon tb-${networkIcon}`} style={{ color: nerworkColor }}><div></div></div>
            }
            </div>
            <div className="ts_gs_btn_wrapper">
            {isShopIcon && <CtaButton shoppingText={wall.ThemeRule.shoppingText} ctaClass={'ts_gs_button'} styleCta={styleCta}/>}
            </div>
            
          </div>
          {wall.ThemeRule.socialAction ? <SocialActions item={item} wall={wall} network={network} />:''}
        </div>
    );
  }
}
