import React, { PureComponent } from "react";
import Image from "./Image";
import {convertObjectToArray} from '../../../../utils'
import reactImageSize from 'react-image-size';
import HotSpot from "../../Elements/HotSpot";
export default class SocialCardMedia extends PureComponent {
  constructor(props) {
    super(props);
    this.myRef = React.createRef();
    this.state = {
      height: '100',
      width: '100',
    
    };
  }

  componentWillMount() {
    const { item } = this.props;
    if(item.mediaHeight && item.mediaWidth !=""){
      this.setState({
        height : (item.mediaHeight * 100) / item.mediaWidth,
         width : (item.mediaWidth * 100) / item.mediaHeight,
      })
    }
      else{
        reactImageSize(item.postFileNew).then(({ width, height }) => this.setState({
          height: (height * 100) / width,
          width: (width * 100) / height
        })).catch((errorMessage) =>
        this.setState({
          height: 100,
          width: 100,
          error: errorMessage
        }))
      }
      }

  render() {    
    const { item , wall} = this.props;
    const { width, height} = this.state
    const ugc_products = convertObjectToArray(item.ugc_products); 
    return (
      <div className="ts_gs_media_wrap">
        
          {width ?<Image  ImageClass={'ts_gs_image'} height={height} width={width} ImageUrl={item.postFileNew} ugc_products={ugc_products} item={item} wall={wall} />:''}
      </div>
    );
  }
}